.rd-cat-cont {
  -webkit-user-drag: none;
  position: relative;
  z-index: 0;
  transition: 0.3s;
  background-color: #f8f8f8;
  padding: clamp(10px, 2vw, 30px);
  aspect-ratio: 1/1.5;
  box-sizing: border-box;
  &:hover {
    z-index: 1;
    background-color: #fff;
    box-shadow: 0 20px 45px -10px rgba(0, 0, 0, 0.15);
  }
  .wish {
    position: absolute;
    z-index: 10;
    color: #aaa;
    svg{

    }
    svg:hover {
      color: red;
    }
  }
  a{
    top: 0;
    .rd-p-box {
      ///////////////////////
      user-select: none;
      /////////////////////////
      position: relative;
      height: 100%;
      .rd-p-img {
        // max-width: 400px;
        // width: 100%;
        // min-width: 200px;
        width: clamp(50px, 100%, 400px);
        aspect-ratio: 1/1;
        // -webkit-user-drag: none;
        img {
          width: 100%;
          height: 100%;
          object-fit: contain;
          // -webkit-user-drag: none;
        }
      }
      .rd-p-info {
        position: absolute;
        bottom: 0;
        .rd-p-name {
          line-height: 1.5;
          letter-spacing: -0.05em;
          margin-bottom: clamp(10px, 1vw, 20px);
          font-size: clamp(12px, 1.2vw, 24px);
        }
        .rd-p-price {
          margin-bottom: clamp(10px, 1vw, 20px);
          font-size: clamp(12px, 1.2vw, 24px);
        }
        .rd-p-color-box {
          display: flex;
          gap: 5px; /* 색상 원 사이의 간격 */
          margin-top: 10px; /* 색상 박스의 상단 여백 */
          .color-circle-wrap {
            cursor: pointer;
            border-radius: 50%;
            background-color: #fff;
            &.on {
              border: 1px solid #aaa;
            }
            .color-circle {
              margin: 3px;
              width: 1.5vw;
              min-width: 18px;
              max-width: 30px;
              aspect-ratio: 1/1;
              border-radius: 50%;
              border: 1px solid #ccc;
              transition: 0.2s;
              &:hover {
                scale: 1.3;
              }
            }
          }
        }
      }
    }
  }
 
}
.mySwiper-rd {
  padding-bottom: clamp(70px, 20vw, 100px);
  .swiper-wrapper {
  }
  .swiper-button-prev,
  .swiper-button-next {
    // position: absolute;
    top: 90%;
    // top: 100%;
    width: 30px;
    height: 30px;
    padding: 8px;
    border: 1px solid #131317;
    border-radius: 50%;
    transition: opacity 0.3s ease;
    color: #131317;
    background: #fff;
    &::after {
      scale: 0.5;
    }
    &:hover {
      background-color: #484848;
      border: 2px solid #9191ff;
      color: #fff;
    }
  }

  .swiper-button-prev {
    left: 50%;
    translate: calc(-50% - 40px);
    &::after {
    }
  }
  .swiper-button-next {
    left: 50%;
    translate: calc(-50% + 40px);
    &::after {
    }
  }

  .swiper-pagination {
    width: auto !important;
    left: 50% !important;
    transform: none;
    translate: -50% !important;
  }
}
@media screen and (max-width: 1000px) {
  .mySwiper-rd {
    .swiper-button-prev,
    .swiper-button-next {
       transform: translateY(-20px);
    }
  }
}
@media screen and (max-width: 768px) {
  .mySwiper-rd {
    .swiper-button-prev,
    .swiper-button-next {
      width: 20px;
      height: 20px;
      //  transform: translateY(-20px);
    }
  }
}
@media screen and (max-width: 500px) {
  .mySwiper-rd {
    .swiper-button-prev,
    .swiper-button-next {
      transform: translateY(-10px);
    }
  }
}
@media screen and (max-width: 330px) {
  .mySwiper-rd {
    .swiper-button-prev,
    .swiper-button-next {
      transform: translateY(10px);
    }
  }
}
