#wish_detail {
  width: clamp(40vw, 50vw, 70vw);
  margin: 0 auto;
  h1 {
    padding-top: 15vh;
    padding-bottom: 5vh;
    font-size: clamp(36px, 5vw, 72px);
    text-align: center;
  }
  .detail-box {
    .left {
      margin: 0 auto;
      padding: clamp(10px, 3vw, 40px);
      box-sizing: border-box;
      .sign-in {
        padding: clamp(10px, 2vw, 20px);
        line-height: 1.7;
        border: 1px solid #00000025;
        span:first-child {
          font-weight: bold;
        }
        a {
          text-decoration: underline;
          font-weight: bold;
        }
      }
      h3 {
        font-size: clamp(20px, 3vw, 30px);
        margin: clamp(20px, 3vw, 30px) 0;
      }
      .cart-list-box {
        max-height: 1200px;
        overflow-y: auto;
        &:hover:focus{
          
        }
        .cart-item {
          transition: .3s;
          position: relative;
          margin: clamp(10px, 1vw, 30px) 0;
          box-sizing: border-box;
          padding-left: clamp(10px, 1vw, 30px);
          padding-top: clamp(20px, 3vw, 30px);
          padding-bottom: clamp(10px, 1vw, 30px);
          padding-right: clamp(20px, 3vw, 50px);
          background-color: #f8f8f8;
          display: flex;
          gap: clamp(10px, 1vw, 30px);
          &:hover{
            background-color: #fff;
    box-shadow: 0 20px 45px -10px rgba(0, 0, 0, 0.15);
          }
          .del {
            position: absolute;
            right: clamp(10px, 1.2vw, 30px);
            top: clamp(10px, 1.2vw, 30px);
            background-color: #ffffff00;
            border: 0;
            cursor: pointer;
            font-size: clamp(20px, 3vw, 30px);
          }
          .cart-img {
            width: clamp(25%, 50vw, 30%);
            img {
              aspect-ratio: 1/1;
              width: 100%;
              object-fit: contain;
            }
          }
          .cart-info {
            flex: 1;
            line-height: 1.3;
            display: flex;
            gap: clamp(5px, 2vw, 20px);
            flex-direction: column;
            .item-name {
              width: 90%;
              font-weight: bold;
              font-size: clamp(12px, 1.5vw, 30px);
            }
            .item-color {
              font-size: clamp(12px, 1.2vw, 24px);
            }
            .item-num {
              display: flex;
              justify-content: space-between;
              flex-wrap: wrap;
              gap: 10px;
              .item-cnt {
                button {
                  cursor: pointer;
                  // font-weight: bold;
                  background-color: #fff;
                  &:hover {
                    background-color: #0000001a;
                  }
                  width: clamp(30px,2vw,40px);
                  padding: 5px;
                  border-radius: 50%;
                  aspect-ratio: 1/1;
                  border: 0;
                }
                span {
                  font-size: clamp(16px,2vw,20px);
                  padding: 0 10px;
                }
              }
              .item-price {
                font-weight: bold;
                font-size: clamp(12px, 1.5vw, 30px);
              }
            }
          }
        }
      }
      .row{
        display: flex;
        justify-content: space-between;
        padding: 0 clamp(20px,2vw,30px);
      }
    }
